export const getPhysicalValue = (item) => {
	switch (item.testNameTypeEnum) {
		case "HEIGHT_WEIGHT":
			item.name = "BMI";
			let bmi = (item.value2 / 1000) / ((item.value1 / 1000) ** 2);
			item.value = bmi.toFixed(1);
			item.levelText = parseLevel(item.level);
			item.type = 1;
			item.unit = "";
			break;
		case "VITAL_CAPACITY":
			item.name = "";
			item.value = item.value1;
			item.levelText = parseLevel(item.level);
			item.type = 2;
			item.unit = "";
			break;
		case "RUN50":
			item.name = "50";
			item.value = item.value1 / 1000;
			item.levelText = parseLevel(item.level);
			item.type = 3;
			item.unit = "";
			break;
		case "STANDINGLONGJUMP":
			item.name = "";
			item.value = item.value1 / 10;
			item.levelText = parseLevel(item.level);
			item.type = 4;
			item.unit = "";
			break;
		case "SITANDREACH":
			item.name = "";
			item.value = item.value1 / 10;
			item.levelText = parseLevel(item.level);
			item.type = 5;
			item.unit = "";
			break;
		case "SITANDUP":
			item.name = "";
			item.value = item.value1;
			item.levelText = parseLevel(item.level);
			item.type = 6;
			item.unit = "";
			break;
		case "PULLUP":
			item.name = "";
			item.value = item.value1;
			item.levelText = parseLevel(item.level);
			item.type = 7;
			item.unit = "";
			break;
		case "RUN1000":
			item.name = "1000";
			item.value = parseTime(item.value1);
			item.levelText = parseLevel(item.level);
			item.type = 8;
			item.unit = "";
			break;
		case "RUN800":
			item.name = "800";
			item.value = parseTime(item.value1);
			item.levelText = parseLevel(item.level);
			item.type = 9;
			item.unit = "";
			break;
		case "UPPERABDOMEN":
			item.name = "";
			item.unShow = true;
			item.levelText = parseLevel(item.level);
			item.type = 10;
			item.unit = "";
			break;
		case "RUN":
			item.name = "";
			item.value = parseTime(item.value1);
			item.levelText = parseLevel(item.level);
			item.type = 11;
			item.unit = "";
			break;
		case "RETURNRUN":
			item.name = "50*4";
			item.value = parseTime(item.value1);
			item.levelText = parseLevel(item.level);
			item.type = 12;
			item.unit = "";
			break;
		case "ROLEJUMP":
			item.name = "";
			item.value = item.value1;
			item.levelText = parseLevel(item.level);
			item.type = 13;
			item.unit = "";
			break;
		case "VISION":
			item.name = "";
			item.unShow = true;
			item.levelText = parseLevel(item.level);
			item.type = 14;
			item.unit = "";
			break;
	}
	return item;
}

