const parseLevel = (level) => {
	let levelText = "";
	switch (level) {
		case "EXCELLENT":
			levelText = "";
			break;
		case "GOOD":
			levelText = "";
			break;
		case "PASS":
			levelText = "";
			break;
		case "NOPASS":
			levelText = "";
			break;
		case "NOGIVEN":
			levelText = "";
			break;
		default:
			levelText = "";
			break;
	}
	return levelText;
}
//  -> 
export const parseTime = (value) => {
	let times = value / 1000;
	let second = times % 60;
	let min = (times - second) / 60;
	let valueText = ""
	if (min) {
		valueText += min + ""
	}
	valueText += second.toFixed(0) + ""
	return valueText;
}

//   
export const getDateTime = (second) => {
	const days = Math.floor(second / 86400)
	const hours = Math.floor((second % 86400) / 3600)
	const minutes = Math.floor(((second % 86400) % 3600) / 60)
	const seconds = Math.floor(((second % 86400) % 3600) % 60)
	let duration = ''
	if (days) {
		duration += `${days}`
	}
	if (hours) {
		duration += `${hours}`
	}
	if (minutes) {
		duration += `${minutes}`
	}
	if (seconds) {
		duration += `${seconds}`
	}
	return duration
}

// 
export const throttle = (fn, wait = 500, isImmediate = false) => {
	let flag = true;
	if (isImmediate) {
		return function() {
			if (flag) {
				fn.apply(this, arguments);
				flag = false;
				setTimeout(() => {
					flag = true
				}, wait)
			}
		}
	}
	return function() {
		if (flag == true) {
			flag = false
			setTimeout(() => {
				fn.apply(this, arguments)
				flag = true
			}, wait)
		}
	}
}

// 
export const debounce = (fn, wait = 500, isImmediate = false) => {
	let timerId = null;
	let flag = true;
	if (isImmediate) {
		return function() {
			clearTimeout(timerId);
			if (flag) {
				fn.apply(this, arguments);
				flag = false
			}
			timerId = setTimeout(() => {
				flag = true
			}, wait)
		}
	}
	return function() {
		clearTimeout(timerId);
		timerId = setTimeout(() => {
			fn.apply(this, arguments)
		}, wait)
	}
}

export const formatDate = date => {
	var date = new Date(date);
	var YY = date.getFullYear() + '-';
	var MM = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
	var DD = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate());
	var hh = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
	var mm = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
	var ss = (date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds());
	return hh + mm + ss;
}

/**
 * 
 * @param nowDate number  
 * @param count number 
 * @returns {string}
 */
export const getDateTimeStrByDate = (nowDate, count) => {
	const dd = new Date(new Date().setTime(new Date().getTime() +
		3600 * 1000 *
		24 * nowDate))
	const timestamp = dd.getTime()
	const newDate = new Date(timestamp + count * 24 * 3600 * 1000)
	const y = newDate.getFullYear()
	let m = (newDate.getMonth() + 1).toString()
	m = m.length === 1 ? '0' + m : m + ''
	const d = newDate.getDate().toString().length === 1 ? '0' + newDate.getDate() : newDate.getDate()
	return y + '-' + m + '-' + d
}

/**
 * 
 * @param nowDate number  
 * @param count number 
 * @returns {string}
 */
export const getDateTimeStrByMonth = (nowDate, count) => {
	const endDate = new Date(new Date().setTime(new Date().getTime() +
		3600 * 1000 *
		24 * nowDate))
	endDate.setMonth(endDate.getMonth() + count);
	const year = endDate.getFullYear()
	let month = endDate.getMonth() + 1
	month = month < 10 ? '0' + month : month;
	const day = endDate.getDate()
	const start = year.toString() + '-' + month.toString() + '-' + day.toString()
	return start
}

/**
 * 
 */
export function dateFormat(date, format) {
	format = format || 'yyyy-MM-dd hh:mm:ss'

	if (date !== 'Invalid Date') {
		const o = {
			'M+': date.getMonth() + 1, //month
			'd+': date.getDate(), //day
			'h+': date.getHours(), //hour
			'm+': date.getMinutes(), //minute
			's+': date.getSeconds(), //second
			'q+': Math.floor((date.getMonth() + 3) / 3), //quarter
			S: date.getMilliseconds(), //millisecond
		}
		if (/(y+)/.test(format)) {
			format = format.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
		}
		for (const k in o) {
			if (new RegExp('(' + k + ')').test(format)) {
				format = format.replace(
					RegExp.$1,
					RegExp.$1.length === 1 ? o[k] : ('00' + o[k]).substr(('' + o[k]).length)
				)
			}
		}
		return format
	}
	return ''
}

/**
 * 
 * 
 * @param date  
 * @returns {string}
 * */
export function getWeekbyDate(date) {
	date = date || new Date()
	let weekDay = date.getDay()
	if (weekDay === 0) {
		weekDay = 7
	}
	return getDateTimeStrByDate(0, -weekDay + 1)
}

/**
 * 
 * @param {number} n 
 */
const getDecimalLength = (n) => {
	const decimal = n.toString().split('.')[1]
	return decimal ? decimal.length : 0
}
/**
 * 
 * @param {number} a 
 * @param {"+"|"-"|"*"|"/"} type 
 * @param {number} b 
 * @example
 * ```js
 * // 
 * const res = computeNumber(1.3, "-", 1.2).next("+", 1.5).next("*", 2.3).next("/", 0.2).result;
 * ```
 */
export const computeNumber = (a, type, b) => {
	/**
	 * 
	 * @description  `33.33333*100000 = 3333332.9999999995` && `33.33*10 = 333.29999999999995` 
	 * @param {number} n
	 */
	const amend = (n, precision = 15) => parseFloat(Number(n).toPrecision(precision))
	const power = Math.pow(10, Math.max(getDecimalLength(a), getDecimalLength(b)))
	let result = 0

	a = amend(a * power)
	b = amend(b * power)

	switch (type) {
		case '+':
			result = (a + b) / power
			break
		case '-':
			result = (a - b) / power
			break
		case '*':
			result = (a * b) / (power * power)
			break
		case '/':
			result = a / b
			break
		default:
			break
	}

	result = amend(result)

	return {
		/**  */
		result,
		/**
		 * 
		 * @param {"+"|"-"|"*"|"/"} nextType 
		 * @param {number} nextValue 
		 */
		next(nextType, nextValue) {
			return computeNumber(result, nextType, nextValue)
		},
	}
}

//    
export const getDateSpeed = (second) => {
	second = second || 0
	const minutes = Math.floor(second / 60)
	const seconds = Math.floor(second % 60)
	let duration = ''
	if (minutes) {
		duration += `${minutes}'`
	}
	duration += `${seconds}''`
	return duration
}
/**
 * 
 * 
 * @param data
 * @returns 0%
 */
export const dealPersent = (data) => {
	let number = data * 100
	if (!number) {
		number = 0
	} else if (number && number % 1 !== 0) {
		const num2 = number.toFixed(3)
		number = num2.substring(0, num2.lastIndexOf('.') + 3)
	}
	return number
}
/**
 *  
 * @param data
 */
export const dealNumber = (data) => {
	if (!data) {
		return 0
	}
	if (data && data % 1 !== 0) {
		const num2 = data.toFixed(3)
		return num2.substring(0, num2.lastIndexOf('.') + 3)
	}
	return data
}
/**
 * 
 * 
 *
 * */
export const dateNew = (date) => {
	const arrDate = []
	const da = date || new Date()
	const dates = new Date(da)
	dates.setDate(1)
	let month = parseInt(dates.getMonth() + 1 + '') + ''
	let day = dates.getDate() + ''
	if (parseInt(month) < 10) {
		month = '0' + month
	}
	if (parseInt(day) < 10) {
		day = '0' + day
	}
	const startData = dates.getFullYear() + '-' + month + '-' + day
	arrDate.push(startData)
	const currentMonth = dates.getMonth() + 1
	const firstDay = new Date(dates.getFullYear(), currentMonth, 1).getTime()
	const oneDay = 1000 * 60 * 60 * 24
	const lastTime = new Date(firstDay - oneDay)
	let endMonth = parseInt(lastTime.getMonth() + 1 + '') + ''
	let endDay = lastTime.getDate() + ''
	if (parseInt(endMonth) < 10) {
		endMonth = '0' + endMonth
	}
	if (parseInt(endDay) < 10) {
		endDay = '0' + endDay
	}
	const endData = dates.getFullYear() + '-' + endMonth + '-' + endDay
	arrDate.push(endData)
	return arrDate
}
